import { _decorator, Component, find, instantiate, Label, Node, Prefab, resources } from 'cc';
import { Uimanager } from '../manager/Uimanager';
import { GameManager } from '../manager/GameManager';
import { clientEvent } from '../utils/clientEvent';
const { ccclass, property } = _decorator;

@ccclass('HomePanel')
export class HomePanel extends Component {
    @property(Label)
    txtGold: Label = null;

    @property(Label)
    txtSoliderLv: Label = null;
    @property(Label)
    txtSoliderLvUpCost: Label = null;

    @property(Label)
    txtHpLv: Label = null;
    @property(Label)
    txtHpLvUpCost: Label = null;

    @property(Label)
    txtGoldLv: Label = null;
    @property(Label)
    txtGoldLvUpCost: Label = null;

    protected onLoad(): void {

    }
    start() {

    }

    update(deltaTime: number) {


    }
    // 初始化
    init() {
        this.txtGold.string = "0";
        this.txtGoldLv.string = "获得奖励等级:Lv.1"
        this.txtGoldLvUpCost.string = "100";
        this.txtSoliderLv.string = "兵种等级:Lv.1"
        this.txtSoliderLvUpCost.string = "100";
        this.txtHpLv.string = "血量提升等级:Lv.1"
        this.txtHpLvUpCost.string = "100";
    }
    onBtnStartClick() {
        console.log("打开游戏界面");
        this.showGamePanel();
        this.node.active = false;
    }

    onBtnSoliderUpClick() {
        console.log("点击升级兵种");
    }
    onBtnHpUpClick() {
        console.log("点击升级血量");
    }
    onBtnGoldUpClick() {
        console.log("点击升级获得金币");
    }
    showGamePanel() {
        Uimanager.instance.hidePanel("HomePanel");
        Uimanager.instance.showPanel("GamePanel",()=>{
            clientEvent.dispatchEvent("CreatorPlayer");
        });// 显示游戏界面
        console.log("显示游戏界面");

       
    }
    showGameOverPanel() {
        resources.load("prefab/UI/GameOverPanel", Prefab, (err, prefab) => {
            if (err) {
                console.error("加载错误:", err);
                return;
            }
            if (prefab) {
                let node = instantiate(prefab);
                node.parent = this.node.parent;
            }
        });
    }
}



